<template>
  <v-card class="full text-center pa-6" dark>

      <h1 class="green--text pa-5" v-if="!started"> Click "Start" to start listening to deposits </h1>
      <h1 class="red--text pa-5" v-else> Click "Stop" to stop listening to deposits </h1>
      <v-btn  large  outlined @click="started = !started" :class="{ green: !started, red: started }">{{ started ? "Stop" : "Start"}}</v-btn>
  
  
  </v-card>
</template>
<script>
 

import {mapState} from 'vuex' 
import countries from './../country'
var audio = new Audio('/Ding-ding-sound.mp3');
// import screenContent from "./../screen";
export default {
  name: 'screen',

  data () {
    return {
      countries,
      item: null,
      started: false,
      window: null
      
    }
  },
  watch: {
    started (val) {
        val ? this.start() : this.stop()
    }
  },
  methods: {
    test (data) {
      const countryCode = data.item.customer.country
      console.log(data.item.user);
      const item = {
        amount: data.amount,
        countryCode,
        country:  countries.filter(e => e.code == countryCode)[0].name,
        agent: data.item.user.displayName,
        profileImage: data.item.user.profileImage,
        date: data.item.created_on.split('T')[0]
      }
   
       
      this.window = window.open("/screen/"+btoa(JSON.stringify(item)), "", "top=0,left=0,width="+screen.availWidth+",height="+screen.availHeight);

    },
    start () {
      console.log('Started listening');
      this.io.on('notefy', this.notefy)
    },
    stop () {
      console.log('stoped listening');

      this.io.off('notefy', this.notefy)
    },
    notefy (data) {
      this.test(data)
     
      // this.item = data.item
      // if (this.window){
      //   this.window.close()
      //   this.window = null
      // }

      // this.window = window.open("", "", "top=0,left=0,width="+screen.availWidth+",height="+screen.availHeight);
 


      // this.window.document.write(
      //   screenContent( data.item.user.displayName,
      //   new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(this.item.amount),
      //   this.src
        
      //   ,{
      //   tpc:  this.item.customer.tpc,
      //   country: this.country,
      // }));
      
      audio.play();

    }
  },
  computed: {
    ...mapState({
      io: state => state.api.io  
    }),
    country () {
      if (this.item.customer && this.item.customer.country) {
        const item = this.countries.filter(e => e.code == this.item.customer.country) 
        if (item.length)
          return item[0].name
      }
      return ''
    },
    src () {
      const origin = `${location.protocol}//${location.hostname}` 
      return (this.item && this.item.user && this.item.user.profileImage) ? (origin + ':3000' +  this.item.user.profileImage) :'/img/blank-profile-picture.png'
    }
    
  },
 
  destroyed () {
    this.stop()
    
  }
}
 


</script>

<style scoped>
.full {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
table tr td {
  font-size: 36px;
  padding: 13px;
}
</style>

